@import url(https://fonts.googleapis.com/css2?family=Scada&display=swap);
@import url(https://fonts.googleapis.com/css2?family=EB+Garamond:wght@500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.home-title-header {
    text-align: center;
    background-color:white;
    min-height: 40vh;
    max-height: 40vh;
    display: flex;
    flex-direction: row;
    /* align-items: center; */
    justify-content: center;
    font-size: calc(4vmin);
    color:black;
    vertical-align: bottom;
  }

  .bar{
    margin: 0px;
    padding: 0ps;
    min-height: 15vh;
    max-height: 15vh;
    min-width: 104vw;
    align-items: left;
    display: flex;
    flex-direction: row;
    justify-content: center;
    vertical-align: center;
  }

  .bar-text{
    font-family: 'Scada', sans-serif;
    font-size: calc(10px + 5vmin);
    color: white;
    flex-direction: row;
    display: flex;
  }

  .nav-bar-text{
    font-family: 'EB Garamond', serif;
    font-size: calc(10px + 3vmin);
    max-width: 20vw;
    color: white;
    flex-direction: row;
    display: flex;
    display: inline-block;
    padding-right: 4vw;
  }


  .home-title{
      vertical-align: bottom;
      font-family: 'EB Garamond', serif;
      font-size: calc(10px + 6vmin);
      color: black;
      flex-direction: row;
      display: flex;
  }

  .home-title-letter{
    vertical-align: bottom;
    text-align: center;
    flex-direction: row;
    display: flex;
    /* min-width: 3vw;
    max-width: 3vw; */
    padding: 0.25vw;
  }

  .home-body{
    min-height: 60vh;
    max-height: 60vh;
    background-color:white;
    /* overflow-x: hidden; */
    overflow: visible;
    /* background-color: black; */
  }

  .home-box{
    overflow: hidden;
  }

  .nav-bar{
    min-width: 100vw;
    max-width: 100vw;
    min-height: 10vh;
    max-height: 10vh;

    background-color: #FFF5CA;
    vertical-align: center;
  }
  
  .links{
    text-decoration: none;  
  }
  
.resume-box {
    background-size: 100vh;
    min-width: 100vw;
    max-width: 100vw;
    min-height: 100vh;
    max-height: 100vh;
    align-items: center;
  }

  .resume-img{
    align-items: center;
    min-width: 40vw;
    max-width: 50vw;
    min-height: 80vh;
    max-height: 100vh;
    background-color:black;
    background-image: url(/personal/static/media/resume.9d278194.png);
    background-repeat: no-repeat;
    background-size: contain;
  }
